      <p>
        <a href="https://bbs.nga.cn/read.php?tid=38556702" target="_blank"
          >https://bbs.nga.cn/read.php?tid=38556702
        </a>
      </p>
      <p>观前预警：</p>
      <p>#千早爱音x长崎素世，但角色重度OOC</p>
      <p>#存在过激引用，自娱之作，仅此而已</p>
      <p>#没头没尾小短片，能接受的话请品尝吧</p>
      <p>————</p>
      <p>“总是在做多余的事情……”</p>
      <p>
        结束了一天的课业，本该是大好的休息时间。然而长崎素世看着账号上的信息，却实在无法露出愉悦的表情。
      </p>
      <p>“诶？这不是很好嘛～”</p>
      <p>
        罪魁祸首笑嘻嘻地凑了过来，恬不知耻地指着那一条条留言，甚至还将它们读了出来：
      </p>
      <p>“求问，乐队里面的贝斯手和吉他关系到底如何。”</p>
      <p>
        “虽然看起来冷眼相对，但是之前上台时候可是主动伸手拉对方，还偷偷摸脸呢。”
      </p>
      <p>“表面看起来针锋相对，实际根本就是调情.jpg”</p>
      <p>
        素世皱着眉头，感受着肩膀上逐渐增加的重量，默默挺直了腰背。只是那因为动作而在胸前散开的凌乱粉毛就像它的主人般，令人感到不适的同时还在得寸进尺。
      </p>
      <p>“看来大家都很认可我们的关系呢，soyorin～”</p>
      <p>“都说过别这么叫我了吧？而且这里面有多少是你的小号？”</p>
      <p>“诶～怎么会这么想，这都是大家自己进行的对话呢～啊，soyorin好无情～”</p>
      <p>
        忍无可忍的素世终于伸手，将那靠在自己肩上还不安分地乱嗅的头推开，表情不善地听着爱音大放厥词：
      </p>
      <p>“这样不是很好吗？最近的大家就喜欢这样的风格，肯定会成为热门的！”</p>
      <p>
        “真是爱慕虚荣。你要百合营业找其他人去，乐队账号不是给你这么用的。把这些删掉。”
      </p>
      <p>
        “明明只是分享些平常的互动而已……soyorin这样闹别扭的话，我今天就不理你咯？”
      </p>
      <p>“……你是小学生吗？”</p>
      <p>
        语气坚决地回绝了友人的耍赖，素世再度抬手按住对方凑过来的头，动作却软绵无力被轻易得逞，让那凌乱的粉毛在贴近时与自己的棕发交缠：
      </p>
      <p>“soyorin的味道，好闻～”</p>
      <p>“只是洗发露而已，而且也分给你用了吧？”</p>
      <p>“自己用的话闻不到啦～”</p>
      <p>“又不是灯那样的短发，怎么可能闻不到……”</p>
      <p>
        尽管嘴上没有一句肯定的话语，但对于埋首于自身长发间的友人，素世的不作为的举措本就已经彰显了默许的态度。
      </p>
      <p>
        不过这种安详的氛围，很快随着素世把玩帐号时，再度发现了不协之处而戛然而止——
      </p>
      <p>“真是的，明明是大家的一片好意……”</p>
      <p>
        辩驳的声音渐低，即使是爱音，也很难理直气壮说出“把素世趴在爱音肩头哭泣的画面，设置为介绍之一是运营的需要”，更别说这出自支持者的作品加入了一定的发散延伸，显得画面上两人过于亲昵。
      </p>
      <p>“啊，来听歌吧，最近发现了感觉不错的——”</p>
      <p>
        有些匆忙地从怀抱里脱出，爱音翻动起柜子，片刻后就拿着有线的耳机返回。
      </p>
      <p>
        安然坐在沙发上的素世，本想开口说些“明明有无线耳机”的冷嘲热讽，但在对方将左耳耳机递来并用诚恳的目光看过来时，即使知道是故作乖巧，她也很难拒绝。
      </p>
      <p>
        坐在素世右边的爱音将另半边耳机带上后，粉与棕便再度交织在了一起，而音乐也开始在两人间流淌：
      </p>
      <p>[明明同处一个房间，奈何有一分为二的玻璃界限。]</p>
      <p>[可你却伸手来牵，将我带到外面。]</p>
      <p>“呐，soyorin，谢谢。”</p>
      <p>“突然怎么了？上次拿我的衣服不想还了？”</p>
      <p>“不要破坏气氛啦！”</p>
      <p>[这样的世界，全部是初次体验。]</p>
      <p>[不能总是受你照顾，下次换我展露笑脸。]</p>
      <p>“soyorin，你对一辈子怎么想……啊，是说乐队的事噢？”</p>
      <p>“……到底想说什么，我可不会像灯一样，给你念诗的。”</p>
      <p>“嘿嘿，没事啦……”</p>
      <p>[一起唱歌，无论几十几百遍。]</p>
      <p>“只是想说，很感谢有你的陪伴。”</p>
      <p>夜幕渐深，而歌曲还在放映，而少女们的嬉闹也还在继续……</p>
      <p>——————</p>
      <p>写在最后：</p>
      <p>
        <del>你怎么知道我被翻了三次牌子？</del>
        只是觉得如果写篇文的话，这时候体会到的悸动，在许久后回顾也能再度传达吧
      </p>
