    <el-card shadow="hover">
      <div slot="header">
        <el-row>
          <el-col :span="8">[素爱/OOC预警]恃宠而骄</el-col>
          <el-col :span="8">不存在的竹渊</el-col>
          <el-col :span="8">2024-02-10 19:56</el-col>
        </el-row>
      </div>
      <p>
        <a href="https://bbs.nga.cn/read.php?tid=39271142" target="_blank"
          >https://bbs.nga.cn/read.php?tid=39271142
        </a>
      </p>
      <p>
        欢庆的家宴后，少女们避开长辈畅聊的热闹，缩回了熟悉的卧室中。虽是两人独处，但相互间的氛围却称不上美妙。
      </p>
      <p>“怎么了，在闹什么别扭？”</p>
      <p>
        随手在屏幕上打下一行“我去哄哄被你们气到的A”，棕发的大小姐放下手机看向一旁，宽广的大床上，柔软被面因翻滚和蹬腿而显得凌乱，一只粉色团子正背对着她生闷气。
      </p>
      <p>
        没有在意对方的心情，素世明知故问地挑起话题，语气直白到有些失礼，没有在外表现的温婉与优雅。
      </p>
      <p>“……哼。”</p>
      <p>“从昨天开始就一直这副模样，真的好让人担心呢～”</p>
      <p>
        面对素世那过于甜腻的语调，此时的爱音无法像往常一样自顾自地忽视，声音从布料中溢出而显得沉闷：
      </p>
      <p>“没什么，只是才知道某人有很——要好的学，妹，呢。”</p>
      <p>“吃醋了？”</p>
      <p>
        “不会，才没有，没有这种事。要做什么也是你的本人的自由，也并不是那什么，嗯，但是，我们是算在交往啊，虽然当时还没有……不过和别人这么亲密的话，会让我觉得，嗯，明明一直都是把我放在第一位，但是和别人也会这么做……”
      </p>
      <p>“诶，这不是很平常的事情吗？”</p>
      <p>“但你之前都瞒着我！”</p>
      <p>“所以吃醋了？”</p>
      <p>“……可能是会有点吃醋，稍微一点……”</p>
      <p>
        嘴上说着安抚，实际上几乎句句在火上浇油的素世，满意地看着之前还背对自己、故作冷漠的粉毛大狗朝着自己呲牙咧嘴，好半天才不情愿地吐出两人心知肚明的实话：
      </p>
      <p>“吃醋了。”</p>
      <p>“好孩子好孩子。”</p>
      <p>
        摆出哄小孩架势的素世，脸上带着温柔的笑容，乍一看倒也充满令人安心的母性光辉。对于伸过来的双手，爱音虽然赌气扭过了头，却还是顺着力道倚靠向她的怀里。
      </p>
      <p>
        素世纤细的手指在爱音头上轻抚，将她此前因翻滚而乱成一团的粉发重新梳理顺滑。而在被熟悉的温暖与味道所包围后，爱音这才放松了因为过久紧绷显得有些僵硬酸软的脸颊。
      </p>
      <p>
        两人安静享受着这短暂的温馨，直到重新控制好情绪的爱音再憋出一句“我还没有原谅你。”
      </p>
      <p>“这样啊，还是来说说刚才吧，在长辈面前摆出那种样子可是很失礼的噢。”</p>
      <p>
        按着肩膀将对方稍微推开，素世摆出过分礼貌的姿态直视着爱音的双眼，数落起她的不是。
      </p>
      <p>“什么叫那种样子啊！只是普通朋友的相处方式嘛！”</p>
      <p>“明明之前还在问我‘对一辈子怎么想？’这样的话呢。”</p>
      <p>
        对于拍着枕头发泄情绪的爱音，素世只是继续夹着声音说着过去，仿佛真的因为对方的绝情而悲伤一般。不过爱音自然不会被这种敷衍的表现骗过，继续追问着此前的疑惑：
      </p>
      <p>“所以到底是怎么回事啊？怎么感觉长辈们的表情都不太对劲！”</p>
      <p>“之前这种场合，你都是主动给我夹菜还喂到嘴里的，你这就忘了吗？”</p>
      <p>
        “上次是上次，这次是这次！不对，我什么时候喂你，等等，之前没有在长辈面前吧！……没有……吧？”
      </p>
      <p>
        被素世爆炸性发言震撼到的爱音一时连话都有点说不清楚，甚至开始自我怀疑起来，直到素世飘来一句“那些都是骗人的”才松了口气。
      </p>
      <p>
        趁着爱音被多次打断情绪的当下，素世再度先发制人，拿起了之前放下的手机，轻描淡写地说着：
      </p>
      <p>“这是对你之前乱说话的回礼。”</p>
      <p>“什么嘛……”</p>
      <p>“虽然能理解想坐对面一直看着我，但是你对于我坐你旁边有什么不满吗？”</p>
      <p>
        话题跳跃得突兀，爱音还在理解之时就见到了素世展示的图片——那是她混入粉丝群之后，在正常交际圈之外与陌生人的闲聊瞎扯。
      </p>
      <p>
        虽然并不是什么见不得人的事，但吐槽他人的真实想法被正主看到会有些心虚。尽管在相同图片上还有她此前略有醋意的话语，不过爱音此时更在意的还是另一件事——
      </p>
      <p>“诶，你怎么也跑到粉丝群里去了？！”</p>
      <p>“还不是因为某人今天不给看手机？”</p>
      <p>
        意识到消息来源的爱音立刻选择“倒打一耙”，却被早有预料的素世轻松单手按住，甚至还有余力回复群里浮现的消息：
      </p>
      <p>“[两个醋坛子就更棒了]？别胡说，只有一个醋坛子。”</p>
      <p>“……嘶。”</p>
      <p>
        尽管实际“确认关系”就在不久前，但两人实际共度的时光早已可以说是浓密的程度，别说肢体接触的亲昵，就连坦诚相见的次数也不算少。漫长相处下的了解，加之偶尔嬉戏中的实际验证，两人对彼此的“喜好”也是知根知底——
      </p>
      <p>
        因为攀上腰间的手过于不安分，原本打算略微放置对方的素世被迫终止了网络上的交流回到现实，与自己那还在闹别扭的恋人重新聊起了“偷腥猫”的话题。
      </p>
      <p>“……之前都不和我说。”</p>
      <p>“也不是故意瞒着你的……而且每次我出门喊你你也不来啊！”</p>
      <p>
        开始时显得底气不足的素世，在辩解后倒像是自我说服了一般开始大声，甚至还能得寸进尺地反过来攻击爱音的过错，惹得她恼羞成怒将素世同样扯倒在床上。
      </p>
      <p>
        只是在这场“针锋相对的斗殴”中，素世甚至还有空进行亡羊补牢般的追究，说着“外面的脏衣服不要穿到我床上”，却又在与爱音互相挠痒痒时，毫不在意地与她在床上滚成一团。
      </p>
      <p>
        另一边，未曾熄灭的屏幕上，本该为乐队与live相关、却不知何时聊起情感话题的粉丝群组里，正有大批故事听到一半却被迫断章的长颈鹿兴风作浪。
      </p>
      <p>从话语的注明到误解向的图片剪切，自感慨和祝福到附加上私货的重复。</p>
      <p>
        仗着当事人的温柔体谅与默许，天南海北的粉丝们闹腾着嬉笑着，以调侃的形式抒发着自己的喜悦和祝福。
      </p>
      <p>
        而这股热潮在两人休战中途，爱音抢过手机抽空回了一句气急败坏的“你们！混进去了好多的奇怪的东西啊喂！”后，更是一路偏斜飞驰向了“被单”与“床垫”的讨论。
      </p>
      <p>
        至于接近深夜时关于“原来的A，不需要了”的笑谈，还有“今晚各回各家”这个回复中“今晚”的咬文嚼字，就是正主之一视而不见、刻意放任的结果了。
      </p>
      <p>
        当然，次日午间那句姗姗来迟的“哄好了”，以及由此而生的发散臆测，便是素世与爱音和好后窝在一起，互相抨击着品读的闲谈杂事了。
      </p>
      <p>————</p>
      <p>写在最后：</p>
      <p>你怎么知道有人说“有种日记被人换掉名字发出来的羞耻感！！”</p>
      <p>
        本文最开始设想的场景，是确定关系后，因为害怕过于亲密被长辈发觉，智将soyo故意让醋坛子闹别扭而表现出“两人没那么好”的状态——结果因为做过头了反而被家里人怀疑“两人之间发生什么了吗，怎么今天不互相喂了”
      </p>
      <p>
        以前当朋友处所以和朋友出去没啥问题，现在确定关系再看其他人就全是偷腥猫(哪来的步梦看侑雫约会.jpg)
      </p>
      <p>
        因一时悸动而生的粗糙口嗨，如果能将这份感情传达给某人的话那便再好不过了。
      </p>
      <p>毕竟，恃宠而骄的，不止闹别扭但要好的两位呢。</p>
